.wrapper{
    display: block;
    position: absolute;
    left: 100px;
    top: 50%;
    transform: translate(0, -50%);
    
    cursor: pointer;
    z-index: 10000;
}

.wrapper svg{
    color: #1235a6;

    width: 30px;
    height: 170px;
}

@media (max-width: 840px) {
    .wrapper{
        left: 20px;
    }
}