/* Reset and base styles  */
* {
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}


@font-face {
	font-family: 'Montserrat';
	src: url(../../src/fonts/Montserrat/Montserrat-VariableFont_wght.ttf);
}

@font-face {
	font-family: 'Helvetica';
	src: url('../fonts/helvetica/Helvetica.ttf');
}

.ItemTitle{
	font-family: 'Montserrat';
	font-weight: bold;
	font-size: 40px;
	text-align: center;
	margin-top: 45px;
	margin-bottom: 150px;
}

.cardWrapper {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto 155px;
    display: flex;
    flex-direction: row;
    gap: 10%;
    padding: 0 20px;
}

/* 📱 Мобильные устройства */
@media (max-width: 768px) {
    .cardWrapper {
        flex-direction: column;
        gap: 40px;
        padding: 0 15px;
        margin-bottom: 100px;
    }
}

/* 📱 Очень узкие экраны */
@media (max-width: 480px) {
    .cardWrapper {
        padding: 0 10px;
        gap: 30px;
        margin-bottom: 80px;
    }
}


.MainWrapper{
	height: 2000px;
}