.wrapper{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
    margin: 0 auto;
    margin-bottom: 100px;
    width: 90%;
    flex-wrap: wrap;
    gap: 5%;
    
}

@media (max-width: 768px) {
    .wrapper{
        justify-content: center;
        gap: 0;

    }
}
